import {
    createApp
} from 'vue'
import App from './App.vue'
import "@/utils/routeGuard"
import router from './router'
import antd from 'ant-design-vue'
import 'ant-design-vue/dist/reset.css';
import store from './store'

let app = createApp(App)
app.use(router)
app.use(antd)
app.use(store)
app.mount('#app')