//选项式api写法
import {
    defineStore
} from 'pinia'
import routeList from '@/assets/json/routeList.json'
import router from '@/router'
export const useRouteStore = defineStore('route', {
    state: () => {
        return {
            routeMenu: [],
            routeCreate: false
        }
    },
    getters: {},
    actions: {
        setRouteList({
            to,
            next
        }) {
            this.routeCreate = true
            this.routeMenu = routeList.list
            for (let index = 0; index < routeList.list.length; index++) {
                let item = routeList.list[index];
                this.addRoute(item)
            }
            next({
                path: to.path,
                query: to.query,
                replace: true
            })
        },
        addRoute(item) {
            router.addRoute({
                path: item.path,
                name: item.name,
                component: () => import(`@/views/${item.filePath}.vue`)
            })
        }
    }
})