import router from '@/router/index'
import {
    useRouteStore
} from '@/store/modules/route'
router.beforeEach((to, from, next) => {
    const routeStore = useRouteStore()
    if (!routeStore.routeCreate) {
        routeStore.setRouteList({
            to,
            next
        })
        return false;
    }
    next()
})