import {
  createRouter,
  createWebHashHistory,
  createWebHistory
} from 'vue-router'

const routes = [{
  path: '/',
  name: 'home',
  component: () => import('../views/home/index.vue')
}, {
  path: '/layout',
  name: 'layout',
  component: () => import('../views/layout/index.vue'),
  // children: [{}]
}]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router